@import "./src/assets/styles/colors";
@import "./src/assets/styles/breakpoints";
@import "./src/assets/styles/vars";


.price-card {
    font-family: 'Montserrat';
    background: $white; 
    color: $black;
    padding: 120px 100px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: space-between;
    border-right: $border;
    border-bottom: $border;
        &__title {
            font-weight: 700;
            font-size: 24px;
            line-height: 29px;
            padding-bottom: 30px;
        }
        &__text {
            font-family: 'Lato';
            font-weight: 300;
            font-size: 16px;
            line-height: 19px;
            padding-bottom: 52px;
        }
        &__text span {
            font-weight: 400;
        }
        &__link {
            display: flex;
            gap: 45px;
            cursor: pointer;
        }
        &__price {
            font-weight: 700;
            font-size: 24px;
            line-height: 29px;
        }
}

@media #{$desktop-large} {
    .price-card {
        padding: 120px 60px;
        &__text {
            padding-bottom: 0;
        }
    }
}

@media #{$desktop} {
    .price-card {
        padding: 120px 40px 120px 36px;

        &__link {
            gap: 20px;
        }
    }
}
@media #{$tablet} {
    .price-card {
        padding: 120px 40px 120px 40px;

        &__link {
            gap: 45px;
        }
    }
}

@media #{$mobile} {
    .price-card {
        padding: 80px 20px 80px 20px;

        &__title {
        font-size: 16px;
        line-height: 20px;
        }
        &__text {
            font-size: 14px;
            line-height: 17px;
        }
                &__price {
            font-weight: 700;
            font-size: 24px;
            line-height: 29px;
        }
        &__link {
            gap: 20px;
        }
        &__price {
            font-size: 16px;
            line-height: 20px;
        }
    }
}
